<template>
  <div class="order_container">
      <head-top head-title="订单列表1" goBack="true"></head-top>
      <ul class="order_ul" v-load-more="loadMore" v-if="orderList">
          <li v-for="(item,index) in orderList" :key="index">
              <img :src="imageBaseUrl+item.restaurant_image_url" alt="" class="restaurant_img">
              <section class="order_item_right">
                <section @click="showDetail(item)">
                    <header class="order_item_right_header">
                        <section class="order_header">
                            <h4>
                                <span class="ellipsis">{{item.restaurant_name}}</span>
                                <svg fill="#333" class="arrow_right">
                                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                                </svg>
                            </h4>
                            <p class="order_time">{{item.formatted_created_at}}</p>
                        </section>
                         <p class="order_status">
                                {{item.status_bar.title}}
                        </p>
                    </header>
                    <section class="order_backet">
                        <p class="order_name ellipsis">{{item.basket.group[0][0].name}}{{item.basket.group[0].length>1 ? '等'+item.basket.group[0].length + '件商品' : ''}}</p>
                        <!-- toFixed()四舍五入保留小数 -->
                        <p class="order_amount">￥{{item.total_amount.toFixed(2)}}</p>
                    </section>
                </section>
                <div class="order_again">
                    <compute-time :time="item.time_pass" v-if="item.status_bar.title=='等待支付'"></compute-time>
                    <router-link :to="{path:'/shop',query:{geohash,id:item.restaurant_id}}" tag="span" v-else  class="buy_again">再来一单</router-link>
                </div>
              </section>
          </li>
      </ul>
      <food-guide></food-guide>
      <loading v-show="showLoading"></loading>
      <transition name="router-slid">
          <router-view></router-view>
      </transition>
  </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import headTop from '../../components/header/header'
import loading from '../../components/common/loading'
import computeTime from '../../components/common/computeTime'
import {getImgPath} from '../../components/common/mixin'
import foodGuide from '../../components/footer/footGuide'
import {getOrderList} from '../../service/getData'
import {loadMore} from '../../components/common/mixin'
import {imageBaseUrl} from '../../config/env'
export default {
    components: {
        headTop,
        foodGuide,
        loading,
        computeTime
    },mixins: [loadMore],
    mounted () {
        this.initData();
    },
    methods: {
        ...mapMutations([
            'SAVE_ORDER'
        ]),
        //初始话信息
        async initData()
        {
            if(this.userInfo&&this.userInfo.user_id)
            {
                let res=await getOrderList(this.userInfo.user_id,this.offset);
                this.orderList=[...res];
                this.hideLoading();    
            }else{
                this.hideLoading();  
            }
        },
        hideLoading()
        {
            this.showLoading=false;
        },
        //加载更多
        async loadMore()
        {
            if(this.preventRepeat)
            {
                return;
            }
            this.preventRepeat=true;
            this.offset+=10;
            this.showLoading=true;
            //获取信息
            let res =await getOrderList(this.userInfo.user_id,this.offset);
            this.orderList=[...this.orderList,...res];
            this.hideLoading();
            if(res.length<10)
            {
                return
            }
            this.preventRepeat=false;
        },
        //显示详情页
        showDetail(item)
        {
            this.SAVE_ORDER(item);
            this.preventRepeat=false;
            this.$router.push('/order/orderDetail');
        }
    },computed: {
        ...mapState([
            'userInfo', 'geohash'
        ])
    },data()
    {
        return{
            orderList:null,//订单列表
            offset:0,
            preventRepeat:false,//防止重复获取
            showLoading:true,//加载动画
            imageBaseUrl
        }
    },watch: {
        userInfo:function(value)
        {
            if(this.userInfo&&this.userInfo.user_id&&!this.orderList)
            {
                this.initData();
            }
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.order_ul{
    margin-top: 2.5rem;
    li{
        background: #fff;
        border-bottom:1px solid $bc;
        margin-top: 0.7rem;
        padding:0.7rem;
        display: flex;
         .restaurant_img{
            @include wh(2rem,2rem);
        }
        .order_item_right{
                flex:5;
                padding-left:0.3rem;
                .order_item_right_header{
                display: flex;
                justify-content: space-between;
                 border-bottom: 0.025rem solid #f5f5f5;
                    padding-bottom: .3rem;
                padding-bottom: .3rem;
                .order_header{
                    h4{
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                        line-height: 1rem;
                        @include sc(0.75rem,#333);
                        width: 9rem;
                        svg{
                            @include wh(.4rem,.4rem);
                            fill: #ccc;
                           padding-right: .2rem;
                        }
                    }
                    .order_time{
                          @include sc(.55rem, #999);
                            line-height: .8rem;
                    }
                }
                .order_status{
                    font-size: 0.6rem;
                    color:#333;
                    align-items: center;
                }
            }
        }
    }
}
.order_backet{
    padding:0.5rem 0rem;
    display: flex;
    border-bottom: 0.025rem solid #f5f5f5;
    justify-content: space-between;
    .order_name{
        flex:3;
        @include sc(0.7rem,#333);
    }
    .order_amount{
        @include sc(0.7rem,#f60);
    }
}
.order_again{
    display: flex;
    justify-content: flex-end;
    .buy_again{
        border:0.025rem solid $blue;
        @include sc(0.6rem,$blue);
        padding:0.3rem;
        border-radius: 0.25rem;
        margin-top: 0.4rem;
    }
}
.order_container{
    margin-bottom: 2.3rem;
}
.router-slid-enter-active,.router-slid-leave-active{
    transition: all .4s;
}
.router-slid-enter,.router-slid-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



