<template>
  <div class="page">
      <span @click="gotoPay" class="rem_time" style="color:orange;border-width:1px;border-style:solid;border-color:orange">
          {{remaining}}
      </span>
      <alert-tip v-show="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import alertTip from '../../components/common/alertTip'
export default {
    props:['time'],
    created () {
        //每次减去服务器的支付剩余时间来和服务器同步
        this.countNUm-=this.numTime;
         this.remainingTime();
    },
    data()
    {
        return{
            countNUm:900,
            showAlert:false,
            alertText:null,
        }
    },components: {
        alertTip
    },methods: {
        closeTip()
        {
            this.$emit('closeTip');
        },
        gotoPay()
        {
            this.showAlert=true;
            this.alertText="暂不开放支付接口";
        },
        //计算时间
        remainingTime()
        {
            clearInterval(this.tiemr);
            this.tiemr=setInterval(()=>{
                this.countNUm--;
                if(this.countNUm==0)
                {
                    clearInterval(this.tiemr);
                    this.alertText="支付超时";
                    this.showAlert=true;
                }
            },1000)
        }
    },computed: {
        //转换时间
        remaining:function()
        {
            let miute=parseInt(this.countNUm/60);
            let second=parseInt(this.countNUm%60);
            if(miute<10)
            {
                miute='0'+miute;
            }
            if(second<10)
            {
                second='0'+second;
            }
            return '去支付(还剩'+miute+'分'+second+'秒)';
        },
        //订单返回时间秒分分别处理
        //和服务器同步剩余时间
        numTime()
        {
            console.log(this.time)
            if(this.time.toString().indexOf('分钟')!==-1)
            {
                return parseInt(this.time)*60;
            }else{
                return parseInt(this.time);
            }
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.page{
    display: inline-block;
    .rem_time{
        @include sc(.55rem,orange);
        padding:.1rem .2rem;
        border-radius: .15rem;
    }
}
</style>



